.contact-container {
    text-align: center;
    margin: 50px auto;
    max-width: 600px;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 10px;
    background-color: #f9f9f9;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .contact-info {
    font-size: 18px;
    line-height: 1.8;
  }
  
  .contact-info strong {
    color: #333;
  }
  